	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2006-11-30-internet-identity-workshop-2006/index.md absPath of file >>> MarkdownRemark","html":"<p>I and Trent are headed to the Internet Identity Workshop this next week. This is one of those hard to figure out areas that is becoming more and more important. We hope to have some kind of solution in place with Ozmozr within the next year or so. Hopefully, this workshop will help us nail down what it is that we are going to do.\nA few weeks ago I attended the Web 2.0 Summit. Yahoo talked about opening their authentication to the world and since then I have seen a few people using that. They have half a billion registered users. That might mean they win the identity game with numbers.\nAt least it will be warmer in San Francisco. It has been around 12-20 degrees here.</p>","frontmatter":{"title":"Internet Identity Workshop 2006","date":"November 30, 2006","imageUrl":null,"image":null}}},"pathContext":{"path":"/internet-identity-workshop-2006"}}

